import React from 'react';
import { HashRouter as Router, Routes, Route, Navigate } from 'react-router-dom'; // <- CAMBIADO A HashRouter
import MainLayout from './MainLayout';
import PublicPage from './PublicPage';
import EquiposPage from './EquiposPage';
import MatchesPage from './MatchesPage';
import RankingPage from './RankingPage';
import AdminLogin from './AdminLogin';
import AdminEquiposPage from './admin/AdminEquiposPage';
import AdminPartidosPage from './admin/AdminPartidosPage';
import PrivateAdminRoute from './PrivateAdminRoute';
import { useAuth } from './AuthContext';

function AppRoutes() {
  const { isAdmin, user } = useAuth();

  if (user === undefined) {
    return null; // o un loading spinner si prefieres
  }

  return (
    <Routes>
      <Route element={<MainLayout />}>
        <Route path="/" element={<PublicPage />} />
        <Route path="/equipos" element={<EquiposPage />} />
        <Route path="/partidos" element={<MatchesPage />} />
        <Route path="/ranking" element={<RankingPage />} />

        <Route
          path="/admin"
          element={isAdmin ? <Navigate to="/admin/equipos" /> : <AdminLogin />}
        />

        <Route
          path="/admin/equipos"
          element={
            <PrivateAdminRoute>
              <AdminEquiposPage />
            </PrivateAdminRoute>
          }
        />
        <Route
          path="/admin/partidos"
          element={
            <PrivateAdminRoute>
              <AdminPartidosPage />
            </PrivateAdminRoute>
          }
        />
      </Route>
    </Routes>
  );
}

export default function App() {
  return (
    <Router>
      <AppRoutes />
    </Router>
  );
}
